@keyframes beat {
    to {
        transform: scale(1.4);
    }
}

@keyframes logoTextColorChange {
    0% {
        color: rgb(0, 0, 0, 0);
    }

    50% {
        color: #ff001b;
    }

    100% {
        color: rgba(255, 255, 255, 1);
    }
}

