:root {
  --font-main: "Roboto", sans-serif;
  --font-size: 16px;
  --text-color-main: #000;
  --text-color-accent: #8c8c8c;
  --text-color-light: #fff;
  --accent-color: #ff6b01;
  --accent-color-light: #fc8028;
  --bg-color: #f7f7f7;
  --menu-line-color: #ff001b;
  --card-shadow: 0px 5px 10px 5px #223c5033, 0px -5px 10px -5px #223c5033;
  --btn-shadow: drop-shadow(0px 8px 43px #ff6b0199);
  --gutter: 32px;
  --gutter-mob: 20px;
  --section-padding: 60px;
  --transition-prop: .25s ease;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-main);
  font-size: var(--font-size);
  color: var(--text-color-main);
}

body.modal-open {
  overflow: hidden;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p, a {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.visual-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

a {
  color: inherit;
  text-decoration: none;
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media screen and (max-width: 767.9px) {
