    max-width: 1280px;
    padding-left: var(--gutter);
    padding-right: var(--gutter);
  }
}

.no-scroll {
  overflow: hidden;
}

.backdrop {
  -webkit-backdrop-filter: saturate(.8) brightness(.9);
  backdrop-filter: saturate(.8) brightness(.9);
}

.section-title {
  text-transform: uppercase;
  color: var(--text-color-main);
  margin-top: 10px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
}

@media screen and (min-width: 768px) and (max-width: 1279.9px) {
