@keyframes beat {
  to {
    transform: scale(1.4);
  }
}

@keyframes logoTextColorChange {
  0% {
    color: #0000;
  }

  50% {
    color: #ff001b;
  }

  100% {
    color: #fff;
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: #0000004d;
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.spinner {
  height: 300px;
  width: 300px;
  background: #f5f1ed;
  border-radius: 10px;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader__text {
  color: var(--accent-color);
  text-transform: uppercase;
  letter-spacing: 1px;
  z-index: 2;
  margin: 5px auto;
  font-family: Anton, sans-serif;
  position: relative;
}

.loader__filmstrip {
  width: 56px;
  height: 70px;
  background: var(--accent-color);
  z-index: 1;
  position: relative;
}

.loader__filmstrip:before, .loader__filmstrip:after {
  content: "";
  height: 120%;
  border-left: 5px dashed #f5f1ed;
  animation: roll .4s infinite;
  position: absolute;
}

.loader__filmstrip:before {
  left: 5px;
}

.loader__filmstrip:after {
  right: 5px;
}

@keyframes roll {
  0% {
    top: 0;
  }

  100% {
    top: -15px;
  }
}

.btn {
  min-width: 130px;
  min-height: 44px;
  cursor: pointer;
  color: var(--text-color-main);
  text-transform: uppercase;
  transition: all var(--transition-prop);
  background-color: #0000;
  border: 1px solid #fff;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 13px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  display: inline-flex;
}

